.toolbar {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
  margin-bottom: 1rem;
}
.filters {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  align-items: center;
}
.filter-input {
  width: 180px;
  padding: 0.5rem 0.75rem;
  border: 1px solid var(--border);
  border-radius: 6px;
  font-size: 0.95rem;
}
.actions { white-space: nowrap; }
.actions .btn { margin-left: 0.35rem; }
th.sortable { cursor: pointer; user-select: none; }
th.sortable:hover { background: rgba(0,0,0,0.05); }
.empty { color: var(--text-muted); text-align: center; padding: 1.5rem !important; }

.modal {
  position: fixed;
  inset: 0;
  background: rgba(0,0,0,0.4);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 1rem;
}
.modal-content {
  max-width: 480px;
  width: 100%;
  max-height: 90vh;
  overflow-y: auto;
  h3 { margin: 0 0 1rem; font-size: 1.2rem; }
}
.modal-actions {
  display: flex;
  gap: 0.75rem;
  justify-content: flex-end;
  margin-top: 1rem;
}
